<template>
  <div class="main-frame">
  </div>
</template>

<script>
// load THREE.js
import * as THREE from 'three'
import OC from 'three-orbit-controls'

export default {
  data () {
    return {
      scene: undefined,
      camera: undefined,
      control: undefined,
      renderer: undefined,
      mixer: undefined,
      prevFrame: 0
    }
  },
  mounted () {
    this.scene = new THREE.Scene()
    let OrbitControls = OC(THREE)

    this.camera = new THREE.PerspectiveCamera(45, window.innerWidth / window.innerHeight, 1, 50)
    this.camera.position.set(0, 10, 25)
    this.camera.lookAt(this.scene.position) // look at 0, 0, 0

    this.renderer = new THREE.WebGLRenderer({ antialias: true })
    this.renderer.setSize(this.$el.clientWidth, this.$el.clientHeight)
    this.renderer.shadowMap.enabled = true
    this.renderer.shadowMap.type = THREE.PCFSoftShadowMap
    this.$el.appendChild(this.renderer.domElement)

    this.control = new OrbitControls(this.camera, this.renderer.domElement)
    this.control.update()

    // add some light sources
    let light1 = new THREE.PointLight(0xffffff, 0.5, 100)
    let light2 = new THREE.PointLight(0xffffff, 0.5, 100)
    let light3 = new THREE.PointLight(0xffffff, 0.5, 100)
    let light4 = new THREE.PointLight(0xffffff, 0.5, 100)
    let light5 = new THREE.PointLight(0xffffff, 0.5, 100)
    // four corner
    light1.position.set(20, 20, 20)
    light2.position.set(-20, 20, 20)
    light3.position.set(20, 20, -20)
    light4.position.set(-20, 20, -20)
    // one at bottom
    light5.position.set(0, -20, 0)
    this.scene.add(light1)
    this.scene.add(light2)
    this.scene.add(light3)
    this.scene.add(light4)
    this.scene.add(light5)

    // add a plane as floor
    let groundGeometry = new THREE.PlaneGeometry(20, 20, 8, 8)
    let groundMaterial = new THREE.MeshStandardMaterial({
      color: 0x5f9ea0,
      side: THREE.DoubleSide,
      metalness: 0.0
    })
    let ground = new THREE.Mesh(groundGeometry, groundMaterial)
    ground.rotateX(-Math.PI / 2)
    this.scene.add(ground)

    // add stickman model
    let loader = new THREE.JSONLoader()
    loader.load('static/model/stickman.json', (geometry) => {
      let female = new THREE.SkinnedMesh(geometry,
        new THREE.MeshStandardMaterial({
          color: 0xff0080,
          skinning: true,
          metalness: 0.0
        })
      )
      this.scene.add(female)

      this.mixer = new THREE.AnimationMixer(female)
      let clips = geometry.animations
      let clip = clips[0]
      let cycle = this.mixer.clipAction(clip)
      cycle.play()

      // use standing as the initial pose
      console.log('initial frame: ', this.playFrame)
      this.mixer.update(this.playFrame || 0)
      this.prevFrame = this.playFrame || 0
    })

    this.animate()
  },
  watch: {
    playFrame: function (val) {
      console.log('switch to frame: ', val)
      this.mixer.update((val - this.prevFrame) || (-this.prevFrame))
      this.prevFrame = val
    }
  },
  props: ['playFrame'],
  methods: {
    animate () {
      requestAnimationFrame(this.animate)
      this.control.update()
      this.renderer.render(this.scene, this.camera)
    }
  }
}
</script>

<style scoped>
.main-frame {
  position: relative;
  top: 1%;
  bottom: 1%;
  right: 1%;
  width: 77%;
  height: 98%;
  background-color: #000000;
  float: right;
  border-radius: 3px;
  box-shadow: 0px 0px 10px 1px;
}
</style>



